.footer__inner, .contact_section .contact__block, .promises_section .promises__block, .benefits__block {
  display: flex;
  justify-content: space-between;
}

.footer__inner, .contact_section .contact__block, .promises_section .promises__block, .benefits__block {
  display: flex;
  justify-content: space-between;
}

.info .info__bg {
  padding: 45px 0;
  background: url("../img/info/info-bg-image.jpg") no-repeat top left;
}
.info .info__block {
  padding: 45px 0;
  margin-left: auto;
  margin-right: 0;
  max-width: 770px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: start;
}
.info .info__list {
  min-width: 370px;
  width: 100%;
  text-align: center;
}
.info .info__list .list__item:not(.list__item:last-of-type) {
  padding-bottom: 40px;
  margin-bottom: 40px;
  position: relative;
}
.info .info__list .list__item:not(.list__item:last-of-type)::after {
  position: absolute;
  content: "";
  width: 100px;
  height: 1px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #85BBB1;
}
.info .info__list .list__item .dollar {
  position: relative;
  padding-left: 31px;
}
.info .info__list .list__item .dollar::before {
  position: absolute;
  content: "";
  background: url("../img/info/$.svg") no-repeat;
  width: 21px;
  height: 50px;
  bottom: 0;
  left: 0;
}
.info .info__list .list__item .count {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 68px;
  line-height: 72px;
  letter-spacing: normal;
  margin-bottom: 12px;
}
.info .info__list .list__item .list__description {
  display: block;
  margin-top: 12px;
}
.info .info__description {
  margin-bottom: 50px;
}
.info .info__description .description:not(.description:last-of-type) {
  margin-bottom: 35px;
}

.benefits__block {
  flex-wrap: wrap;
  row-gap: 60px;
  column-gap: 30px;
}
.benefits__block .benefit_item {
  padding-top: 30px;
  border-top: 1px solid #E4E4E4;
  max-width: 570px;
}
.benefits__block .benefit_item__inner {
  padding: 20px 35px 52px 35px;
}
.benefits__block .benefit_item__title {
  margin-bottom: 30px;
}

.quote_block {
  background: linear-gradient(0deg, rgba(25, 28, 41, 0.84), rgba(25, 28, 41, 0.84)), url("../img/quote/quote-background.jpg") no-repeat center top;
  background-size: cover;
  color: #FFFFFF;
}
.quote_block .quote {
  max-width: 710px;
  width: 100%;
  margin: 0 auto 0 160px;
  padding: 100px 0;
  box-sizing: unset;
}
.quote_block .quote__text {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 46px;
  letter-spacing: 0.04em;
  margin-bottom: 40px;
  position: relative;
}
.quote_block .quote__text::before {
  position: absolute;
  content: "";
  top: -30px;
  left: -60px;
  background: url("../img/quote/quote-icon-light.svg") no-repeat;
  width: 102px;
  height: 84px;
}
.quote_block .quote__caption {
  font-family: "Playfair Display", serif;
  font-style: italic;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.08em;
}

.button {
  display: block;
  max-width: 300px;
  width: 100%;
  text-align: center;
  padding: 33px 0;
  border: 1px solid #E4E4E4;
  transition: all 0.5s ease;
}
.button:hover {
  border: 1px solid #E64F3A;
}
.button:hover .link {
  color: rgb(229, 79, 58);
}
.button:hover .link::after {
  background-color: rgba(229, 79, 58, 0.3);
}

.video__container {
  max-width: 1000px;
}
.video__container .video__block {
  padding: 31px 70px 31px 30px;
  margin-bottom: 50px;
  border: 1px solid #E4E4E4;
  border-radius: 32px;
  position: relative;
}
.video__container .video__block::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
}
.video__container .video__block::after {
  position: absolute;
  content: "";
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
}
.video__container .video__inner {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  border-radius: 8px;
  overflow: hidden;
}
.video__container .video__inner #iframeVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  z-index: 1;
}
.video__container .video__inner #screensaver {
  max-width: 870px;
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  z-index: 2;
}
.video__container .video__title {
  margin-bottom: 20px;
}
.video__container .video__description {
  max-width: 810px;
  width: 100%;
}

.promises_section .promises__bg {
  padding: 45px 0;
  background: url("../img/promises/promises-bg-image.jpg") no-repeat top left;
}
.promises_section .promises__block {
  padding: 75px 0 75px 70px;
  margin-left: auto;
  margin-right: 0;
  max-width: 770px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  flex-wrap: wrap;
  column-gap: 100px;
  row-gap: 70px;
}
.promises_section .promise__item {
  max-width: 300px;
  width: 100%;
  margin-bottom: 15px;
}
.promises_section .promise__title {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 48px;
  letter-spacing: normal;
  text-transform: capitalize;
}
.promises_section .description:not(.description:last-of-type) {
  margin-bottom: 10px;
}

.reviews_section {
  margin-bottom: 100px;
}
.reviews_section .reviews__title {
  margin-bottom: 20px;
}
.reviews_section .reviews__description {
  max-width: 400px;
  width: 100%;
  margin-bottom: 50px;
}
.reviews_section .reviews__block {
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  gap: 30px;
}
.reviews_section .reviews__block .review__item {
  flex: 0 0 auto;
  width: 33.333%;
  max-width: 370px;
  height: auto;
  color: #FFFFFF;
}
.reviews_section .reviews__block .review__item:nth-child(1), .reviews_section .reviews__block .review__item:nth-child(4), .reviews_section .reviews__block .review__item:nth-child(7) {
  background: url("../img/reviews/reviews-bg-1.jpg") no-repeat;
  background-size: cover;
}
.reviews_section .reviews__block .review__item:nth-child(2), .reviews_section .reviews__block .review__item:nth-child(5), .reviews_section .reviews__block .review__item:nth-child(8) {
  background: url("../img/reviews/reviews-bg-2.jpg") no-repeat;
  background-size: cover;
}
.reviews_section .reviews__block .review__item:nth-child(3n) {
  background: url("../img/reviews/reviews-bg-3.jpg") no-repeat;
  background-size: cover;
}
.reviews_section .reviews__block .review__inner {
  padding: 30px 40px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.reviews_section .reviews__block .review__text {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: normal;
  flex: 1 0 auto;
}
.reviews_section .reviews__block .review__signature {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.08em;
  font-style: italic;
  padding-top: 25px;
}

.contact_section {
  margin-bottom: 100px;
}
.contact_section .contact__block {
  padding-top: 60px;
  border-top: 1px solid #E4E4E4;
}
.contact_section .contact__text {
  padding-top: 21px;
  max-width: 470px;
  width: 100%;
}
.contact_section .contact__text .contact__title {
  margin-bottom: 30px;
}
.contact_section .contact__form {
  max-width: 470px;
  width: 100%;
}
.contact_section .contact__form .form__item {
  display: block;
  margin-bottom: 40px;
  width: 100%;
  height: 32px;
  border: none;
  border-bottom: 1px solid rgba(38, 42, 63, 0.1);
  outline: none;
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-style: italic;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.08em;
}
.contact_section .contact__form .form__item:focus {
  border-bottom: 2px solid rgba(38, 42, 63, 0.3);
}
.contact_section .contact__form .form__item:invalid:focus {
  border-bottom: 2px solid rgba(229, 79, 58, 0.5);
}

.button_link {
  border: none;
  background: none;
  cursor: pointer;
}

.footer__inner {
  padding: 30px 0;
}
.footer__inner .footer__text {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 26px;
  letter-spacing: normal;
  color: rgba(38, 42, 63, 0.6);
}
.footer__inner .footer__link {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.08em;
  opacity: 1;
  transition: all 0.8s ease;
}
.footer__inner .footer__link:hover {
  transform: scale(1.1);
}
.footer__inner .footer__link + .footer__link {
  margin-left: 32px;
}
.footer__inner .footer__link_site {
  margin-left: 12px;
}

.lazyframe[data-vendor=youtube]::after,
.lazyframe[data-vendor=youtube_nocookie]::after {
  width: 120px;
  height: 120px;
  margin: 0;
  transform: translate(-50%, -50%);
  background-image: url("../img/video/icon_play.svg");
  cursor: pointer;
}
.lazyframe[data-vendor=youtube]:hover::after,
.lazyframe[data-vendor=youtube_nocookie]:hover::after {
  background-image: url("../img/video/icon_play.svg");
}
.lazyframe[data-vendor=youtube] .lazyframe__title,
.lazyframe[data-vendor=youtube_nocookie] .lazyframe__title {
  font-size: 0;
  color: rgba(255, 255, 255, 0);
}
.lazyframe[data-vendor=youtube] .lazyframe__title::before,
.lazyframe[data-vendor=youtube_nocookie] .lazyframe__title::before {
  background: none;
}

@media screen and (max-width: 1220px) {
  .reviews_section .reviews__block {
    justify-content: space-evenly;
  }
  .reviews_section .reviews__block .review__item {
    width: 50%;
  }
  .benefits .benefits__block {
    justify-content: space-evenly;
    gap: 30px;
  }
  .benefits .benefits__block .benefit_item {
    flex: 0 0 45%;
  }
  .benefits .benefits__block .benefit_item .benefit_item__title {
    word-break: break-word;
  }
}
@media screen and (max-width: 1060px) {
  .quote_block .quote {
    margin: 0 auto 0 80px;
    padding: 50px 0;
  }
}
@media screen and (max-width: 1000px) {
  .header .header__menu {
    transform: translateX(100%);
    overflow: auto;
    position: fixed;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    right: 0;
    top: 0;
    bottom: 0;
    width: 0;
    height: 100vh;
    padding: 0;
    box-sizing: border-box;
    z-index: 1;
    transition: transform 0.8s ease;
    background: white;
  }
  .header .header__menu.active {
    transform: translateX(0);
    width: 100%;
    padding: 20px;
  }
  .header .header__menu .menu__item {
    margin: 0 !important;
    padding: 15px 0;
    width: 100%;
    text-align: center;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.08em;
  }
  .header .header__burger {
    display: inline-block;
  }
}
@media screen and (max-width: 820px) {
  .title {
    font-size: 30px;
    line-height: 38px;
    letter-spacing: normal;
  }
  .info .info__list .list__item .count {
    font-size: 56px;
    line-height: 60px;
    letter-spacing: normal;
  }
  .first_scrin {
    padding: 50px 0;
  }
  .info .info__bg {
    padding-bottom: 0;
  }
  .info .info__block {
    flex-wrap: wrap;
    gap: 30px;
  }
  .info .info__list {
    min-width: unset;
    max-width: 370px;
  }
  .section, .reviews_section, .contact_section {
    margin-bottom: 50px;
  }
  .lazyframe[data-vendor=youtube]::after,
.lazyframe[data-vendor=youtube_nocookie]::after {
    width: 80px;
    height: 80px;
  }
  .video__container .video__block::after {
    width: 20px;
    height: 20px;
  }
  .quote_block .quote {
    margin: 0 auto;
    padding: 40px 0;
  }
  .quote_block .quote .quote__text {
    font-size: 24px;
    line-height: 38px;
    letter-spacing: 0.06em;
  }
  .quote_block .quote .quote__text::before {
    background: none;
  }
  .quote_block .quote .quote__caption, .quote_block .quote .quote__text {
    padding: 0 15px;
  }
  .promises_section .promises__bg {
    padding: 45px 15px;
    background: linear-gradient(0deg, rgba(25, 28, 41, 0.84), rgba(25, 28, 41, 0.84));
  }
  .promises_section .promises__bg .promises__block {
    padding: 15px;
    gap: 20px;
    justify-content: center;
  }
  .reviews_section .reviews__block {
    justify-content: center;
  }
  .reviews_section .reviews__block .review__item {
    width: 100%;
  }
  .benefits .benefits__block {
    justify-content: space-evenly;
    gap: 15px;
  }
  .benefits .benefits__block .benefit_item {
    flex: 0 0 100%;
  }
  .contact_section .contact__block {
    padding-top: 30px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }
  .footer .footer__inner {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}